import { MDCSnackbar } from '@material/snackbar'
import SnackbarOption from './models/snackbar-options'
import SnackbarOpenOption from './models/snackbar-open-option'

interface SetTemplateReturnObjectItem {
  snackBar: HTMLElement
  snackBarContainer: HTMLElement
}

export default class MATSnackbar {
  private options: SnackbarOption
  private snackBar: any
  private snackBarContainer: any

  constructor(options: SnackbarOption) {
    this.options = options
  }

  open(data: SnackbarOpenOption) {
    let template = this.setTemplate(data)
    const snack = new MDCSnackbar(template.snackBar)
    snack.getDefaultFoundation().setTimeoutMs(this.options.timeout || 4000)
    var app = document.querySelector('#app') as HTMLElement
    app.appendChild(template.snackBarContainer)
    snack.open()
    setTimeout(() => {
      app.removeChild(template.snackBarContainer)
    }, snack.getDefaultFoundation().getTimeoutMs() + 50 || 500)
  }

  setSnackbar(data: SnackbarOpenOption) {
    let template = this.setTemplate(data)
    this.snackBarContainer = template.snackBarContainer
    this.snackBar = new MDCSnackbar(template.snackBar)
    this.snackBar.getDefaultFoundation().setTimeoutMs(this.options.timeout || 4000)
    var app = document.querySelector('#app') as HTMLElement
    app.appendChild(template.snackBarContainer)
  }

  getSnackbar(): MDCSnackbar {
    return this.snackBar
  }

  close() {
    var app = document.querySelector('#app') as HTMLElement
    app.removeChild(this.snackBarContainer)
  }

  private setTemplate(data: SnackbarOpenOption): SetTemplateReturnObjectItem {
    /* eslint-disable */
    var mdc_snackbar_container = document.createElement('div')
    var mdc_snackbar = document.createElement('div')
    var mdc_snackbar__serface = document.createElement('div')
    var mdc_snackbar__label = document.createElement('div')
    var mdc_snackbar__action = document.createElement('div')
    var mdc_snackbar__action_button = document.createElement('button')

    mdc_snackbar_container.style.position = 'relative'
    mdc_snackbar_container.style.zIndex = '30'
    mdc_snackbar_container.style.width = '100%'

    mdc_snackbar.classList.add(
      'mdc-snackbar',
      // this.options.isDismissable ? 'mdc-snackbar__dismiss' : 'xxx',
      this.options.isLeading ? 'mdc-snackbar--leading' : 'xxx',
      this.options.isStacked ? 'mdc-snackbar--stacked' : 'xxx'
    )

    mdc_snackbar__serface.classList.add('mdc-snackbar__surface')

    mdc_snackbar__label.classList.add('mdc-snackbar__label')
    mdc_snackbar__label.setAttribute('role', 'status')
    mdc_snackbar__label.setAttribute('aria-live', 'polite')

    if (data.validationErrors) {
      let ul = document.createElement('ul')
      ul.classList.add('snackbar-validation-error-list')
      data.validationErrors.forEach(element => {
        let li = document.createElement('li')
        li.innerHTML = `
        <div class="list-inner">
            <div class="icon"><i class="material-icons">error</i></div>
            <div class="text">${element}</div>
        </div>
        `
        ul.appendChild(li)
      })
      mdc_snackbar__serface.appendChild(ul)
    }

    if (data.text) {
      mdc_snackbar__label.innerHTML = `${data.text}`
    }

    mdc_snackbar__action.classList.add('mdc-snackbar__actions')

    mdc_snackbar__action_button.classList.add('mdc-button', 'mdc-snackbar__action')
    mdc_snackbar__action_button.setAttribute('type', 'buton')

    mdc_snackbar__action_button.innerHTML = `
    <i class="material-icons">
      close
    </i>`

    mdc_snackbar__action.appendChild(mdc_snackbar__action_button)
    mdc_snackbar__serface.appendChild(mdc_snackbar__label)
    mdc_snackbar__serface.appendChild(mdc_snackbar__action)
    mdc_snackbar.appendChild(mdc_snackbar__serface)
    mdc_snackbar_container.appendChild(mdc_snackbar)

    return { snackBar: mdc_snackbar, snackBarContainer: mdc_snackbar_container }
  }
}
