
















import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({})
export default class HomePageSectionTeamCard extends Vue {
  @Prop({ default: '' }) name: any
  @Prop({ default: '' }) position: any
  @Prop({ default: '' }) image: any

  getImgUrl(image: string) {
    var images = require.context('@/assets/img/team', false, /(\.png|\.jpg|\.jpeg)$/)
    return images('./' + image)
  }
}
