





















































































































































































import { Vue, Component } from 'vue-property-decorator'
import { MDCTextField } from '@material/textfield'
import { MDCSelect } from '@material/select'
import Axios from 'axios'
import MATSnackbar from '@/utils/mat-snackbar'
import getRecaptchar from '@/utils/google-recaptcha'

@Component({})
export default class RegistrationPageSectionRegistrationForm extends Vue {
  isProcessing: boolean = false
  isRegistered: boolean = false
  private snackbar: MATSnackbar
  private firstName: MDCTextField = {} as MDCTextField
  private lastName: MDCTextField = {} as MDCTextField
  private email: MDCTextField = {} as MDCTextField
  private batchCode: MDCSelect = {} as MDCSelect
  private indexNumber: MDCTextField = {} as MDCTextField
  private secretKey: MDCTextField = {} as MDCTextField

  constructor() {
    super()
    this.snackbar = new MATSnackbar({ isDismissable: true, timeout: 4000 })
  }

  mounted(): void {
    this.initForm()
    // console.log(this.isIndexNumberValid('17001961d'))
  }

  onSubmit(): void {
    if (this.isFormValid()) {
      getRecaptchar('registration')
        .then(response => {
          if (response.success) {
            const formData = new FormData()
            formData.append('firstName', this.firstName.value)
            formData.append('lastName', this.lastName.value)
            formData.append('email', this.email.value)
            formData.append('batchCode', this.batchCode.value)
            formData.append('indexNumber', this.indexNumber.value)
            formData.append('secretKey', this.secretKey.value)
            formData.append('recaptcharToken', `${response.token}`)
            this.isProcessing = true
            Axios.post(`${process.env.VUE_APP_ROOT_API}/spp/register`, formData)
              .then((response: any) => {
                if (response.data.success) {
                  this.isRegistered = true
                } else {
                  if (response.data.validation_error) {
                    if (response.data.validation_error.userExist) {
                      this.snackbar.open({ text: "You're already registered." })
                    }
                    if (response.data.validation_error.isKeyCorrect === false) {
                      this.snackbar.open({ text: 'Secret key is invalid.' })
                    }
                  } else {
                    this.snackbar.open({ text: 'Error! Try again.' })
                  }
                }
              })
              .catch((error: any) => {
                this.snackbar.open({ text: 'Error! Try again.' })
              })
              .finally(() => {
                this.isProcessing = false
              })
          }
        })
        .catch(error => {
          this.snackbar.open({ text: 'Refresh this page and try again.' })
        })
    }
  }

  initForm(): void {
    this.firstName = new MDCTextField(document.querySelector('.text-field-first_name') as HTMLElement)
    this.lastName = new MDCTextField(document.querySelector('.text-field-last_name') as HTMLElement)
    this.email = new MDCTextField(document.querySelector('.text-field-email') as HTMLElement)
    this.batchCode = new MDCSelect(document.querySelector('.mdc-select-batch_code') as HTMLElement)
    this.indexNumber = new MDCTextField(document.querySelector('.text-field-index_number') as HTMLElement)
    this.secretKey = new MDCTextField(document.querySelector('.text-field-secret_key') as HTMLElement)

    this.indexNumber.listen('keypress', (event: KeyboardEvent) => {
      const regX = /^[0-9]$/g
      if (event.keyCode != 13) {
        if (!regX.test(`${event.key}`)) {
          event.preventDefault()
        }
      }
    })

    this.firstName.listen('keypress', (event: KeyboardEvent) => {
      const regX = /^[a-zA-z]$/g
      if (event.keyCode != 13) {
        if (!regX.test(`${event.key}`)) {
          event.preventDefault()
        }
      }
    })

    this.lastName.listen('keypress', (event: KeyboardEvent) => {
      const regX = /^[a-zA-z]$/g
      if (event.keyCode != 13) {
        if (!regX.test(`${event.key}`)) {
          event.preventDefault()
        }
      }
    })
  }

  isFormValid(): boolean {
    if (this.firstName.value.trim() == '') {
      this.snackbar.open({ text: 'Please enter your first name.' })
      return false
    } else if (this.lastName.value.trim() == '') {
      this.snackbar.open({ text: 'Please enter your last name.' })
      return false
    } else if (this.email.value.trim() == '') {
      this.snackbar.open({ text: 'Please enter your email.' })
      return false
    } else if (!this.isEmailValid(this.email.value.trim())) {
      this.snackbar.open({ text: 'Please enter a valid email.' })
      return false
    } else if (this.batchCode.value.trim() == '') {
      this.snackbar.open({ text: 'Please select a batch code.' })
      return false
    } else if (this.indexNumber.value.trim() == '') {
      this.snackbar.open({ text: 'Please enter the index number.' })
      return false
    } else if (!this.isIndexNumberValid(this.indexNumber.value.trim())) {
      this.snackbar.open({ text: 'Please enter a valid index number.' })
      return false
    } else if (this.secretKey.value.trim() == '') {
      this.snackbar.open({ text: 'Please enter the secret key.' })
      return false
    } else {
      return true
    }
  }

  private isEmailValid(email: string): boolean {
    // eslint-disable-next-line
    const regx_email = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return regx_email.test(email)
  }

  private isIndexNumberValid(number: string): boolean {
    const regx_index1 = /[1][6-8][0][0|2][0-9][0-9][0-9][1-9]$/g
    const regx_index2 = /[1-9][0-9][0-9][0-9][0-9][0-9]$/g
    if (number.length == 8) {
      return regx_index1.test(number)
    } else {
      return regx_index2.test(number)
    }
  }
}
