declare var grecaptcha: any

class RecaptcharResult {
  constructor(public success: boolean, public error: Error | null, public token: string | null) {}
}

const getRecaptchar = (action: string): Promise<RecaptcharResult> => {
  return new Promise((resolve, reject) => {
    try {
      grecaptcha.ready(() => {
        grecaptcha.execute(`${process.env.VUE_APP_CAPTCHA_SITE_KEY}`, { action: action }).then((token: string) => {
          resolve(new RecaptcharResult(true, null, token))
        })
      })
    } catch (error) {
      reject(new RecaptcharResult(false, error, null))
    }
  })
}

export default getRecaptchar
